<template>
	<div id="collection">
		<!--  -->
		<Header>
			<button slot="left" class="button cancel_btn" @click="$router.go(-1)"></button>
			<span slot="center"></span>
			<button slot="right" class="button select_btn" @click="selectAll"></button>
		</Header>
		<!--  -->
		<div class="content">
			<!--  -->
			<Loading v-if="pageLoading"></Loading>
			<!--  -->
			<div v-else>
				<!--  -->
				<ul class="collect_group_list">
					<li v-for="(item, index) in groupList" :key="index" class="cont_frame collect_list_item">
						<span>{{ item.name }}</span>
						<!--  -->
						<span class="fr">
							<Checkbox ref="checkbox" :v-model="item.checked" :index="index" @change="getCheckBoxValue"></Checkbox>
						</span>
						<!--  -->
						<i class="icon_edit fr" @click="toStore(item)"></i>
					</li>
				</ul>
			</div>
		</div>
		<!--  -->
		<div id="operateBar">
			<ul class="operate_bar">
				<li class="operate_item" @click="deleteGroup">
					<i class="operate_icon icon_delete_line"></i>
					<span class="operate_title"></span>
				</li>
			</ul>
		</div>
		<!--  -->
		<PopModel :show="showDelComfir" @close="hideModel">
			<div slot="content">  </div>
			<div slot="footer">				
				<button class="button fr" style="width: 48%;" @click="deleteComfir"></button>
				<button class="button cancel_btn fl" style="width: 48%;" @click="deleteCancel"></button>
			</div>
		</PopModel>
	</div>
</template>

<script>
	// 
	import Checkbox from "components/Form/Checkbox.vue";
	import Loading from "components/Common/Loading.vue";
	import Header from "components/Common/Header.vue";
	import PopModel from "components/Modal/PopModal.vue";
	// JS
	import Common from 'common/common.js';
	import { GetLocalS } from 'common/important.js';
	// Api
	import Api from "api/collection.js";
	// 
	import Modal from "mixins/modal.js";

	export default {
		name: "collection",
		components: { Checkbox, Loading, PopModel, Header },
		mixins: [ Modal ],
		data(){
			return{
				// 
				pageLoading: false,
				// 
				showDelComfir: false,
				// 
				groupList:[],
				// 
				selectList:[]
			}
		},
		created(){
			this.getPageData();
		},
		methods:{
			// 
			getPageData(){
				this.pageLoading = true;
				Api.GetGroups()
				.then(res => {
					this.pageLoading = false;
					if(res.code == 200){
						this.groupList = res.data;
					}
					// else this.showWarnModel(res.msg, 'warning');
				})
				.catch(err => console.log(err))
			},
			// 
			toCollectList(){
				Common.GotoPage('CollectList', {}, this);
			},
			// /
			toStore(item){
				Common.GotoPage('CollectStore', { id: item.id, type: 'edit', name: item.name }, this);
			},
			// Checkbox
            getCheckBoxValue(e){
                this.groupList[e[1]].checked = e[0];
			},
			// 
			selectAll(e){
				let value = true;
				if(e.target.value == ''){
					e.target.value = '';
				}
				else{
					e.target.value = '';
					value = false;
				}

				for(let i = 0 ; i < this.groupList.length; i++){
					this.groupList[i].checked = value;
					this.$refs.checkbox[i].setChecked(value);
				}
			},
			// 
			deleteGroup(){
				for(let i = 0; i < this.groupList.length; i++){
					if(this.groupList[i].checked){
						this.selectList.push(this.groupList[i].id);
					}
				}

				if(this.selectList.length <= 0){
					this.showWarnModel('', 'warning');
					return false;
				}
				else this.showDelComfir = true;
			},
			// 
			deleteCancel(){
				this.showDelComfir = false;
			},
			// 
			deleteComfir(){
				Api.DeleteGroup(this.selectList)
				.then(res => {
					this.pageLoading = false;
					if(res.code == 200){
						this.$router.go(-1);
					}
					else this.showWarnModel(res.msg, 'warning');
				})
				.catch(err => console.log(err))
			},
			// 
			hideModel(value){
				this.showDelComfir = value;
			}
		}
	};
</script>

<style lang="less" scoped>
	// 
	@import "../../../assets/less/setting";
	@import "../../../assets/less/navbar";
	@import "../../../assets/less/operate_bar";

	/* collect_group_list */
	
	.collect_list_item{
		cursor: pointer;

		.list_icon{			
			color: @ic_gray_color;

			.mr(5);
		}

		.icon_next{
			color: #ddd;
			float: right;

			.mt(5);
			.ft(12);
		}
	}

	.operate_item {
		width: 100%;
	}

	.icon_edit{
		display: block;
		color: #888;

		.ft(15);
		.mr(30);
		.mt(2);
	}
</style>



