<template>
	<div id="register">		
		<section class="register edge_frame">
			<h1 class="register_title"> </h1>
			<form>
				<div class="form_line">
					<input type="text" v-model="form.username" placeholder=""/>
				</div>
				<div class="form_line">
					<input type="text" class="fl" v-model="form.validCode" placeholder="" style="width:60%;" />
					<input type="button" class="fr button" value="" @click="getCode" style="width:38%;" />
				</div>
				<div class="form_line">
					<input type="text" v-model="form.password" placeholder=""/>
				</div>
				<div class="form_line">
					<Checkbox :v-model="form.password" @change="getCheckBoxValue"><a class="agreement" href="http://www.baidu.com"></a></Checkbox>
				</div>
				<div class="form_line">
					<input type="button" class="button" value="" @click="validForm"/>
				</div>
				<div class="form_line">
					<router-link class="button login_btn" :to="{ name: 'Login' }"></router-link>
				</div>
			</form>
		</section>
		<!--  -->
		<Copyright></Copyright>
	</div>
</template>

<script>
	// 
	import Copyright from "components/Common/Copyright.vue";
	import Checkbox from "components/Form/Checkbox.vue";
	// Api
	// import Api from "api/register.js";

	export default {
		name: "register",
		components: { Copyright, Checkbox },
		data(){
			return{
				// 
				form:{
					// 
					username: '',
					// 
					validCode: '',
					// 
					password: '',
					/// 
					agree: true
				},
			}
		},
		created(){
		},
		methods:{
			// 
			validForm(){
				if(this.form.username == ''){
					this.showWarnModel('', 'warning');
					return false;
				}
				else if(this.form.validCode == ''){
					this.showWarnModel('', 'warning');
					return false;
				}
				else if(this.form.password == ''){
					this.showWarnModel('', 'warning');
					return false;
				}
				else if(!this.form.agree){
					this.showWarnModel('', 'warning');
					return false;
				}
				else{
					this.onSubmit();
				}
			},
			// 
			onSubmit(){
			},
			// 
			getCode(){

			},
			// Checkbox
            getCheckBoxValue(e){
                this.form.agree = e[0];
            }
		}
	};
</script>

<style lang="less" scoped>
	// 
	@import "../../assets/less/setting";

	.register{
		position: fixed;
		width: 100%;
		top: 50%;
		.mt(-200);
	}

	.agreement{
			color: @base_color;
			text-decoration: underline;
		}

	.register_title{
		text-align: center;
		color: @base_color;
		.mb(20);
	}

	.login_btn{
		background: @cancel_btn_color;

		&:hover{
			color: #fff;
		}
	}

	/* layout */
	@media screen and (min-width: 960px) {
		.register {
			width: @wrapper_max_w;
		}
	}

</style>


