<template>
	<div id="newsDetail">
        <!--  -->
		<NavBar title="" :is-second-page="true"></NavBar>
        <!--  -->
		<div class="content">
            <!--  -->
            <Loading v-if="pageLoading"></Loading>
            <!--  -->
            <div v-else>
                <div class="cont_frame">
                    <!--  -->
                    <h2 class="article_title">{{ newsCont.title }}</h2>
                    <!--  -->
                    <section class="article_time">{{ newsCont.publishDate }}</section>
                    <section class="article_tags">{{ newsCont.keyWord }}</section>
                    <!--  -->
                    <section class="article_cont" v-html="newsCont.htmlContext">{{ newsCont.htmlContext }}</section>
                    <!--  -->
                    <section class="article_hint">* </section>
                    <!--  -->
                    <section class="article_org_link">
                        <!-- <a @click="openScoure(newsCont.url)">{{ newsCont.webSite }} <span v-if="newsCont.url">()</span></a> -->
                        <a @click="getScoure(newsCont.url)">{{ newsCont.webSite }} <span v-if="newsCont.url">()</span></a>
                    </section>
                    <!--  -->
                    <section class="select_artc">
                        <a class="fl" @click="readPrev(newsCont.prevID)"><i class="icon_back"></i> </a>
                        <a class="fr" @click="readNext(newsCont.nextID)"> <i class="icon_next"></i></a>
                        <div class="clearfix"></div>
                    </section>
                </div>
                <!--  -->
                <div id="operateBar">
                    <ul class="operate_bar">
                        <li class="operate_item" @click="collect">                  
                            <div v-if="newsCont.isCollect">
                                <i class="operate_icon icon_collection is_collected"></i>
                                <span class="operate_title is_collected"></span>
                            </div>
                            <div v-else>
                                <i class="operate_icon icon_collection_line"></i>
                                <span class="operate_title"></span>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <!--  -->
            <ScrollModal :show="showSoucreModel">
                <!--  -->
                <i class="icon_close" slot="h_right" @click="hideSoucre"></i>
                <div slot="h_center"></div>
                <div slot="content" class="news_source">
                    <div class="news_source_cont">
                        <iframe id="sourceUrl" frameborder="0" width="100%" height="100%" :src="newsUrl" sandbox="allow-scripts allow-same-origin">
                        </iframe>
                    </div>
                </div>
            </ScrollModal>
            <!--  -->
            <ScrollModal :show="showSelectModel">
                <!--  -->
                <button slot="h_left" class="button cancel_btn" @click="hideSelectModel"></button>
                <div slot="h_center"></div>
                <button slot="h_right" class="button comfir_btn" @click="saveCollect"></button>
                <div slot="content">
                    <div class="add_group" @click="showAddGroup"> + </div>
                    <!--  -->
                    <Loading v-if="listLoading"></Loading>
                    <!--  -->
                    <ul v-else class="group_list">                        
                        <li :class="['group_list_item', selectIndex == index ? 'active' : '']" v-for="(item, index) in groupList" :key="index" @click="getGroupSelect(item, index)">{{ item.name }}</li>
                    </ul>
                </div>
            </ScrollModal>
            <!--  -->
            <PopModel :show="showDelComfir" @close="hideDelModel">
                <div slot="content">  </div>
                <div slot="footer">				
                    <button class="button fr" style="width: 48%;" @click="cancelCollect"></button>
                    <button class="button cancel_btn fl" style="width: 48%;" @click="hideDelModel(false)"></button>
                </div>
            </PopModel>
            <!--  -->
            <PopModel :show="showAddModel" @close="hideAddModel">
                <div slot="content">
                    <input ref="groupName" type="text" v-model="groupName" placeholder=""/>
                </div>
                <div slot="footer">				
                    <button class="button fr" style="width: 48%;" @click="addGroup"></button>
                    <button class="button cancel_btn fl" style="width: 48%;" @click="hideAddModel(false)"></button>
                </div>
            </PopModel>
            <!--  -->
            <PopModel :show="showSoModel" @close="hideSouModel">
                <div slot="content">
                    <p></p>
                    <p class="original_link">{{ newsUrl }}</p>
                    <input ref="urlInput" v-model="newsUrl" class="url_input" type="text">
                </div>
                <div slot="footer">				
                    <button class="button fr" style="width: 48%;" @click="copyLink"></button>
                    <button class="button cancel_btn fl" style="width: 48%;" @click="hideSouModel(false)"></button>
                </div>
            </PopModel>
		</div>
	</div>
</template>

<script>
    // 
    import NavBar from "components/Common/NavBar.vue";
	import Loading from "components/Common/Loading.vue";
    import ScrollModal from "components/Modal/ScrollModal.vue";
    import PopModel from "components/Modal/PopModal.vue";
    // Api
    import Api from "api/news.js";
    // 
    import Modal from "mixins/modal.js";
    // url
    import { GetLocalS, GetUrlQuery } from "common/important.js";
    // js
    import Common from 'common/common.js';
    import axios from 'axios';

	export default {
        name: "newsDetail",
        components: { NavBar, Loading, ScrollModal, PopModel },
        mixins: [ Modal ],
		data(){
			return{
                // 
                pageLoading: false,
                // Type
                pageType: null,
                // 
                newsId: 0,
                // 
                newsCont: {
                    group:{
                        id: 0,
                        name: ''
                    },
                    title: '',
                    htmlContext: '',
                    keyWord: '',
                    publishDate: '',
                    url: null,
                    webSite: '',
                    prevID: 0,
                    nextID: 0,
                    isCollect: false
                },
                // 
                isDeleted: false,
                // 
                showSelectModel: false,
                // 
                listLoading: false,
                // 
                groupList: [],
                // 
                selectIndex: -1,
                // 
                showDelComfir: false,
                // 
                showAddModel: false,
                // 
                groupName: '',
                // 
                showSoucreModel: false,
                // 
                newsUrl: '',
                // 
                showSoModel: false,
			}
		},
		created(){
            this.init();
        },
        methods:{
            init(){
                this.pageType = GetUrlQuery('type');
                this.newsId = GetUrlQuery('newsId');
                // type == 1 
                if(this.pageType == 1){
                    // 
                    this.$store.commit('SET_GOBACK_ROUTE', { name: 'ProjectNews', query: {} });
                }
                // type == 2 
                if(this.pageType == 2){
                    // 
                    this.$store.commit('SET_GOBACK_ROUTE', { name: 'Message', query: {} });
                }
                            
                this.getNewsCont(this.newsId);               
            },
            // 
            getGroups(){
                this.listLoading = true;
                Api.GetGroups()
				.then(res => {
                    this.listLoading = false;
					if(res.code == 200){
						this.groupList = res.data;
					}
					else this.showWarnModel(res.msg, 'warning');
				})
				.catch(err => console.log(err))
            },
            // 
            getNewsCont(newsId){
                // 
                this.newsId = newsId;
                // 
				this.pageLoading = true;

				Api.DeclareDetail(newsId)
				.then(res => {
					if(res.code == 200){
                        this.newsCont = res.data;                   
						// 
                        this.pageLoading = false;
                        
                        // 
						this.$nextTick(() => {
                            // img
							$('.article_cont').find('img').remove();
						})
                    }
					else {
                        // 
                        this.pageLoading = false;
                        this.newsCont.htmlContext = res.msg;
                        if(res.code == 0) this.isDeleted = true;
                    }
				})
				.catch(err => console.log(err))
            },
            // 
            readPrev(id){
                if(id !=0){
                    this.gotoArticle(id);
                }
                else this.showWarnModel('', 'warning');
            },
            // 
            readNext(id){
                if(id !=0){
                    this.gotoArticle(id);
                }
                else this.showWarnModel('', 'warning');
            },
            // 
            gotoArticle(id){
                if(this.pageType){
                    Common.GotoPage('NewsDetail', { newsId: id, type: this.pageType }, this);
                }
                else{
                    Common.GotoPage('NewsDetail', { newsId: id }, this);
                }
                this.getNewsCont(id);
            },
            // 
            collect(){
                if(this.isDeleted) this.showWarnModel('', 'warning');
                // 
                if(GetLocalS('project_token')) {
                    // 
                    if(this.newsCont.isCollect){
                        // 
                        this.showDelComfir = true;
                        return true;
                    }
                    // 
                    else{
                        this.showSelectModel = true;
                        this.getGroups();
                    }                    
                }
			    else this.showWarnModel('', 'warning');
            },
            // 
            hideSelectModel(){
                this.showSelectModel = false;
                this.newsCont.group.id = 0;
                this.selectIndex = -1;
            },
            // 
            getGroupSelect(item, index){
                this.newsCont.group.id = item.id;
                this.selectIndex = index;
            },
            // 
            saveCollect(){
                if(this.newsCont.group.id == 0){
                    this.showWarnModel('', 'warning');
                    return false;
                }

                Api.AddArticle({
                    declareId: this.newsId,
                    groupId: this.newsCont.group.id
                })
				.then(res => {
					if(res.code == 200) {
                        this.getNewsCont(this.newsId);
                        this.hideSelectModel();
                    }
					else this.showWarnModel(res.msg, 'warning');
				})
                .catch(err => console.log(err))
            },
            // 
            cancelCollect(){
                Api.DeleteArticle([this.newsId])
				.then(res => {
					this.pageLoading = false;
					if(res.code == 200){
                        this.getNewsCont(this.newsId);
                        this.deleteCancel();
					}
					else this.showWarnModel(res.msg, 'warning');
				})
				.catch(err => console.log(err))
            },
            // 
			hideDelModel(value){
				this.showDelComfir = value;
            },
            // 
            showAddGroup(){
                this.showAddModel = true;
                this.groupName = '';
            },
            // 
            hideAddModel(value){
                this.showAddModel = value;
            },
            // 
            addGroup(){
                if(this.groupName == ''){
                    this.showWarnModel('', 'warning');
                    return false;
                }

                Api.AddGroup(this.groupName)
                .then(res => {
                    this.pageLoading = false;
                    if(res.code == 200){
                        this.getGroups();
                        this.showAddModel = false;
                    }
                    else this.showWarnModel(res.msg, 'warning');
                })
                .catch(err => console.log(err))
            },
            // 
            openScoure(url){
                this.showSoucreModel = true;
                this.newsUrl = url;
            },
            // 
            hideSoucre(){
                this.showSoucreModel = false;
            },
            // 
            getScoure(url){
                this.showSoModel = true;
                this.newsUrl = url;
            },
            // 
            copyLink(){
                this.$refs.urlInput.select();
                document.execCommand("Copy");
                this.showSoModel = false;
            },
            // 
            hideSouModel(value){
                this.showSoModel = value;
            },            
        }
	};
</script>

<style lang="less" scoped>
    @import "../../assets/less/setting";
    @import "../../assets/less/article";
    @import "../../assets/less/operate_bar";

    .operate_item {
		width: 100%;
    }
    
    .is_collected{
        color: @base_color;
    }

    .no_group_list{
        p{
            text-align: center;
        }
    }

    .group_list, .no_group_list{
        margin-top: @navbar_h + 46*@rem;
        width: 100%;
    }

    .group_list_item{
        display: block;
        height: 46*@rem;
        line-height: 46*@rem;
		padding: 0 12*@rem;
		border-bottom: @border_light;
		text-align: center;
		cursor: pointer;

		&:hover{
			color: @base_color;
			background: @base_hover_color;
        }
        
        &.active{
            color: #fff;
			background: @base_color;
        }
    }
    .add_group{
        display: block;
        position: fixed;
        width: 100%;
        top: @navbar_h;
        height: 46*@rem;
        line-height: 46*@rem;
        padding: 0 12*@rem;
        text-align: center;
        border-bottom: 1px solid lighten(@base_color, 30%);
        background: #fff;
        z-index: 99;
        color: @base_color;
        cursor: pointer;
    }

    .news_source{
        margin-top: @navbar_h;
        
        .news_source_cont{
            height: 100vh;
        }
    }

    .original_link{
        color: @base_color;
        overflow-wrap: break-word;
    }

    .url_input{
        position: absolute;
        opacity: 0;
        top:0;
    }
    
    /* layout */
    @media screen and (min-width: 960px) {
        .add_group {
            width: @wrapper_max_w;
        }
    }
</style>



