<template>
	<div id="app">
		<!--  -->
		<SideBar></SideBar>
		<!--  -->
		<transition name="animate">
			<router-view></router-view>
		</transition>
		<!--  -->
		<Copyright></Copyright>
	</div>
</template>

<script>
	// 
	import SideBar from "components/Common/SideBar.vue";
	import Copyright from "components/Common/Copyright.vue";

	export default {
		name: "app",
		components: { SideBar, Copyright },
		data(){
			return{}
		}
	};
</script>



