<template>
	<div id="companyInfo">
        <!--  -->
		<NavBar title="" :is-second-page="true"></NavBar>
		<!--  -->
		<div class="content">
            <!--  -->
            <Loading v-if="pageLoading"></Loading>
            <!--  -->
            <div v-else>
                <img :src="companyPic" alt=""/>
                <div class="cont_frame" v-html="companyCont">
                    {{ companyCont }}
                </div>
            </div>
		</div>
	</div>
</template>

<script>
    // 
    import NavBar from "components/Common/NavBar.vue";
	import Loading from "components/Common/Loading.vue";
	import Copyright from "components/Common/Copyright.vue";
	// Api
	import Api from "api/about.js";

	export default {
		name: "companyInfo",
		components: { NavBar, Loading, Copyright },
		data(){
			return{
				// 
				pageLoading: false,
				companyPic: require('@/assets/images/pic-company.jpg'),
				companyCont: '',
			}
		},
		created(){
			this.init();
		},
		methods:{
			// 
			init(){
				this.getCompnayIntro();
			},
			// 
			getCompnayIntro(){
				// 
				this.pageLoading = true;

				Api.AboutUs()
				.then(res => {
					if(res.code == 200){
                        this.companyCont = res.data;                        
						// 
                        this.pageLoading = false;
					}
					else {
                        // 
                        this.pageLoading = false;
                        this.companyCont= res.msg;
                    }
				})
				.catch(err => console.log(err));
			}
		}
	};
</script>

<style>

</style>



