<template>
	<div id="login">		
		<section class="login edge_frame">
			<h1 class="login_title"> </h1>
			<form>
				<div class="form_line">
					<input type="text" v-model="form.userName" placeholder=""/>
				</div>
				<div class="form_line">
					<input type="password" v-model="form.password" placeholder=""/>
				</div>
				<div class="form_line">
					<Checkbox :v-model="remember" @change="getCheckBoxValue"></Checkbox>
				</div>
				<div class="form_line">
					<input type="button" class="button" value="" @click="validForm"/>
				</div>
				<div class="form_line">
					<input type="button" class="button reg_btn" value="" @click="goBack"/>
				</div>
				<!-- <div class="form_line">
					<router-link class="button reg_btn" :to="{ name: 'Register' }"></router-link>
				</div> -->
			</form>
		</section>
		<!--  -->
		<Copyright></Copyright>
	</div>
</template>

<script>
	// 
	import Copyright from "components/Common/Copyright.vue";
	import Checkbox from "components/Form/Checkbox.vue";
	// js
	import Common from 'common/common.js';
	import { GetLocalS, SetLocalS, Encrypt, Decrypt } from 'common/important.js';
	import { SetAccount } from 'common/account.js';
	// Api
	import Api from "api/login.js";
	// 
	import Modal from "mixins/modal.js";

	export default {
		name: "login",
		components: { Copyright, Checkbox },
		mixins: [ Modal ],
		data(){
			return{
				// 
				form:{
					// 
					userName: '',
					// 
					password: ''
				},
				// 
				remember: false
			}
		},
		created(){
			if(GetLocalS('username') && GetLocalS('password')){
				this.form.userName = GetLocalS('username');
				//
				this.form.password = Decrypt(GetLocalS('password'));
				this.remember = true;
			}
		},
		methods:{
			// 
			validForm(){
				if(this.form.userName == ''){
					this.showWarnModel('', 'warning');
					return false;
				}
				else if(this.form.password == ''){
					this.showWarnModel('', 'warning');
					return false;
				}
				else{
					this.onSubmit();
				}
			},
			// 
			onSubmit(){
				Api.Login(this.form)
				.then(res => {
					if(res.code == 200){
						// 
						SetAccount(this.$store.commit, res.data);

						// 
						if(this.remember){
							SetLocalS('username', this.form.userName);
							//
							SetLocalS('password', Encrypt(this.form.password));
						}

						Common.GotoPage('ProjectNews', {} , this);
						// 
						this.$store.commit('SET_SHOW_SIDE_BAR', false);
					}
					else this.showWarnModel(res.msg, 'warning');
				})
				.catch(err => console.log(err))
			},
			// Checkbox
            getCheckBoxValue(e){
                this.remember = e[0];
			},
			// 
			goBack(){
				this.$router.go(-1);
				// 
				this.$store.commit('SET_SHOW_SIDE_BAR', false);
			}
		}
	};
</script>

<style lang="less" scoped>
	// 
	@import "../../assets/less/setting";

	.login{
		position: fixed;
		width: 100%;
		top: 50%;
		.mt(-200);
	}

	.login_title{
		text-align: center;
		color: @base_color;
		.mb(20);
	}

	.reg_btn{
		background: @cancel_btn_color;

		&:hover{
			color: #fff;
		}
	}
	
	/* layout */
	@media screen and (min-width: 960px) {
		.login {
			width: @wrapper_max_w;
		}
	}
</style>


