import Vue from 'vue'
import Vuex from 'vuex'
import App from './App'
import router from './router'
import store from './store'
import 'babel-polyfill'
// Jquery
import $ from "jquery";

import { GetLocalS } from 'common/important.js';

// 
require('./assets/iconfonts/style.css');

Vue.use(Vuex);

Vue.config.productionTip = false;

// 
const whiteList = [ 'Login', 'UnLogined', 'Register', 'NewsDetail', 'ProjectNews', 'Search', 'About', 'CompanyIntro' ];

// 
// to: Route:  
// from: Route: 
// next: Function:  resolve  next 
router.beforeEach((to, from, next) => {	
	// ABBAscrollTop0
	window.scroll(0, 0);

	// 
	if(whiteList.indexOf(to.name) != -1) next();
	else{
		let isLogined = Boolean(GetLocalS('project_token'));
		// 
		if(isLogined){
			// token
			store.dispatch('CheckToken').then(res => next()).catch(err => next({ name : 'UnLogined'}))
		}
		else next({ name : 'UnLogined'});
	}
});

new Vue({
	el: '#app',
	router,
	store,
	components: { App },
	template: '<App/>'
})



