// cookie
export function SetCookie(cname, cvalue, exdays){
    if(exdays){
        let d = new Date();
        d.setTime(d.getTime() + (exdays*24*60*60*1000));
        let expires = "expires=" + d.toGMTString();
        document.cookie = cname + "=" + cvalue + "; " + expires;
    }else{
        document.cookie = cname + "=" + cvalue + "; ";
    }
}
// cookie
export function GetCookie(cname){
    let name = cname + "=";
    let ca = document.cookie.split(';');
    for(let i = 0; i < ca.length; i ++) {
        let c = ca[i].trim();
        if (c.indexOf(name) == 0) return c.substring(name.length,c.length);
    }
    return "";
}
// cookie
export function DelCookie(cname){
    document.cookie = `${cname}=; expires=Thu, 01 Jan 1970 00:00:00 GMT`;
}
// localstorage
export function SetLocalS(key,value){
    localStorage.setItem(key, JSON.stringify(value)); // JSON
}
// localstorage
export function GetLocalS(key,value){
    let res = localStorage.getItem(key);
    if(res && res!='null') return JSON.parse(res); // JSONJSON
    else return false;
}
// localstorage
export function DelLocalS(key){
    localStorage.removeItem(key);
}
// localstorage
export function ClearLocalS(){
    localStorage.clear();
}
// url
export function GetUrlQuery(name, thisUrl){
    let reg = new RegExp("(^|\\?|&)"+ name +"=([^&]*)(\\s|&|$)", "i"), url = thisUrl || location.href;
if (reg.test(url)) return decodeURI(RegExp.$2.replace(/\+/g, " "));
else return false;
}
// 
export function GetParams(params){
    let obj = { key : [], value: [] };
    for(let i in params){
        obj.key.push(i);
        obj.value.push(params[i]);
    }
    return obj;
}
// 
export function Encrypt(pwd){
    let value = '';
    for (let i = 0; i < pwd.length; i++) {
        let r = pwd.charCodeAt(i);
        value += String.fromCharCode(r + 2);
    }
    return value;
}
// 
export function Decrypt(value){
    let pwd = '';
    for (let i = 0; i < value.length; i++) {
        let r = value.charCodeAt(i);
        pwd += String.fromCharCode(r - 2);
    }
    return pwd;
}

/**
 *  wait
 * @param func 
 * @param wait 
 * @param options {leading: false}
 *                {trailing: false}
 * @returns {Function}
 */
export function Throttle(func, wait, options) {
    var context, args, result;
    var timeout = null;
    var previous = 0;
    if (!options) options = {};
    var later = function() {
        previous = options.leading === false ? 0 : new Date().getTime();
        timeout = null;
        result = func.apply(context, args);
        if (!timeout) context = args = null;
    };
    return function() {
        var now = new Date().getTime();
        if (!previous && options.leading === false) previous = now;
        var remaining = wait - (now - previous);
        context = this;
        args = arguments;
        if (remaining <= 0 || remaining > wait) {
            if (timeout) {
                clearTimeout(timeout);
                timeout = null;
            }
            previous = now;
            result = func.apply(context, args);
            if (!timeout) context = args = null;
        } else if (!timeout && options.trailing !== false) {
            timeout = setTimeout(later, remaining);
        }
        return result;
    };
}


