<template>
	<div id="collection">
		<!--  -->
		<Header>
			<button slot="left" class="button cancel_btn" @click="cancel"></button>
			<span slot="center">{{ navTitle }}</span>
			<button slot="right" class="button select_btn" @click="validForm"></button>
		</Header>
		<!--  -->
		<div class="content">
			<!--  -->
			<Loading v-if="pageLoading"></Loading>
			<!--  -->
			<div v-else class="cont_frame">
				<form>
					<div class="form_line">
						<input ref="groupName" type="text" v-model="form.name" placeholder="" @click="selectInput"/>
					</div>
				</form>
			</div>
		</div>
		<!--  -->
		<Copyright></Copyright>
	</div>
</template>

<script>
	// 
	import Copyright from "components/Common/Copyright.vue";
	import Loading from "components/Common/Loading.vue";
	import Header from "components/Common/Header.vue";
	// JS
	import Common from 'common/common.js';
	import { GetLocalS, GetUrlQuery } from 'common/important.js';
	// 
	import Modal from "mixins/modal.js";
	// Api
	import Api from "api/collection.js";

	export default {
		name: "collection",
		components: { Copyright, Loading, Header },
		mixins: [ Modal ],
		data(){
			return{
				// 
				pageLoading: false,
				// 
				pageType: 'add',
				// 
				navTitle: '',
				// 
				form:{
					// ID
					id: 0,
					// 
					name: '',
				},
			}
		},
		created(){
			this.init();
		},
		methods:{
			// 
			init(){
				this.pageType = GetUrlQuery('type');
				if(this.pageType == 'edit'){
					this.navTitle = '';
					this.form.id = GetUrlQuery('id');
					this.form.name = GetUrlQuery('name');
				}
			},
			// 
			cancel(){
				this.$router.go(-1);
			},
			// 
			selectInput(e){
				e.target.select()
			},
			// 
			validForm(){
				if(this.form.name == ''){
					this.showWarnModel('', 'warning');
					return false;
				}
				else if(this.form.name.length > 10){
					this.showWarnModel('10', 'warning');
					return false;
				}
				else{
					this.onSubmit();
				}
			},
			// 
			onSubmit(){
				// 
				if(this.pageType == 'add'){
					Api.AddGroup(this.form.name)
					.then(res => {
						this.pageLoading = false;
						if(res.code == 200) Common.GotoPage('CollectList', {}, this);
						else this.showWarnModel(res.msg, 'warning');
					})
					.catch(err => console.log(err))
				}
				// 
				else if(this.pageType == 'edit'){
					Api.EditGroup(this.form)
					.then(res => {
						this.pageLoading = false;
						if(res.code == 200) Common.GotoPage('CollectList', {}, this);
						else this.showWarnModel(res.msg, 'warning');
					})
					.catch(err => console.log(err))
				}
				else this.showWarnModel('', 'warning');
			}
		}
	};
</script>

<style lang="less" scoped>
	// 
	@import "../../../assets/less/setting";
	@import "../../../assets/less/navbar";
	@import "../../../assets/less/operate_bar";

	/* collect_group_list */
	
	.collect_list_item{
		cursor: pointer;

		.list_icon{			
			color: @ic_gray_color;

			.mr(5);
		}

		.icon_next{
			color: #ddd;
			float: right;

			.mt(5);
			.ft(12);
		}
	}

	.operate_item {
		width: 100%;
	}
</style>



