export default {
	data() {
		return {}
    },
    methods:{
        // txt type: 
        showWarnModel(modalText, modalType){
            if(modalType){
                this.$store.commit('SET_WARN_MODAL', {
                    show: true,
                    showIcon: 'true',
                    text: modalText,
                    type: modalType
                });
            }
            else{
                this.$store.commit('SET_WARN_MODAL', {
                    show: true,
                    text: modalText
                });
            }
        },
    }
}


