<template>
	<div id="projectNews">
		<!--  -->
		<NavBar :show-title="false" :show-msg="true"></NavBar>
		<!--  -->
		<div class="content">
			<!--  -->
			<Loading v-if="pageLoading"></Loading>
			<!--  -->
			<div v-else>
				<!--  -->
				<NewsList :data="newsList" :num="listNum" :show-tag="true" :page-type="1"></NewsList>
				<div class="clearfix"></div>
				<!--  -->
				<div v-if="loadMore" class="load_more">
					<Loading></Loading>
				</div>
				<!--  -->
				<BackTop v-show="showTopBtn" :has-tab-bar="false"></BackTop>
			</div>
			<div style="height:20px"></div>
		</div>
	</div>
</template>

<script>
	// 
	import NavBar from "components/Common/NavBar.vue";
	import Loading from "components/Common/Loading.vue";
	import BackTop from "components/Common/BackTop.vue";
	import NewsList from "components/News/NewsList.vue";
	// Api
	import Api from "api/news.js";
	import Msg from "api/message.js";
	// 
	import ScrollPage from 'mixins/scrollPage.js';
	import Modal from "mixins/modal.js";
	// js
	import Common from 'common/common.js';
	import { GetLocalS, SetLocalS, DelLocalS } from 'common/important.js';

	export default {
		name: "projectNews",
		components: { NavBar, Loading, BackTop, NewsList },
		mixins: [ ScrollPage, Modal ],
		data() {
			return{
				// 
				pageLoading: false,
				// 
				newsList: [],
				// 
				listScrollH: 0,
			}
		},
		// 
        beforeRouteLeave (to, from, next) {
			// alert(''+to.name);
			// 
			if(to.name === 'NewsDetail') next();
			else{
				// 
				DelLocalS('listNum');
				DelLocalS('scrollH');				
				next();
			}
        },
		created(){
			this.init();
			this.scrollHSet();
		},
		mounted(){
			// 
			window.addEventListener('scroll', this.scrollPage);
		},
		methods:{
			// 
			init(){
				// 
				this.$store.commit('SET_GOBACK_ROUTE', { name: null, query: {} });
				this.getMsg();
			},
			// 
			getMsg(){
				Msg.GetMessage()
				.then(res => {
					if(res.code == 200) this.$store.commit('SET_UN_READ_COUNT', res.data.unRead);
					else this.$store.commit('SET_UN_READ_COUNT', 0);
				})
				.catch(err => console.log(err))
			},
			//  num: more
			getListData(num, more){
				// 
				if(more) this.loadMore = true;
				else this.pageLoading = true;

				Api.DeclareList({
					pageNum: 1,
					pageSize: num
				})
				.then(res => {
					if(res.code == 200){						
						// 
						this.newsList = res.data.result;

						// 
						if(more){
							// 
							if(this.listNum >= res.data.dataCount) this.loadMoreNow = true;
							else this.loadMoreNow = false;
							// 
							this.loadMore = false;
						}
						else{
							// 
							this.pageLoading = false;

							// 
							if(this.listScrollH > 0){
								var _this = this;
								this.$nextTick(() => {
									scrollTo(0, _this.listScrollH);	
								})
							}
						}
					}
					else this.showWarnModel(res.msg, 'warning');
				})
				.catch(err => console.log(err))
			}
		},		
		destroyed(){
			// 
			window.removeEventListener("scroll",this.scrollPage);
		}
	};
</script>

<style lang="less" scoped>
	// 
	@import "../../assets/less/setting";

	.proj_news{
		.news_li_title{
			width: 100%;
			.mb(5);
			.ht(24);
		}

		.news_li_publishDate{
			.mt(5);
		}
	}

	/*  */
	.load_more{
		position: relative;
		border-bottom: @border_deep;
		.ht(80);

		.loading{
			position: absolute;
		}
	}
</style>



