<template>
	<div id="about">
		<!--  -->
		<NavBar title="" :is-second-page="true"></NavBar>
		<!--  -->
		<div class="content">
			<div class="info">
				<img class="icon" :src="logo"/>
				<p></p>
			</div>
			<ul class="info_list">
				<li class="info_list_item cont_frame" @click="gotoCompanyInfo" style="cursor: pointer;">
					<i class="icon_next"></i>
				</li>
				<li class="info_list_item cont_frame">
					
					<span class="fr"><i class="tips"></i>V1.2</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	// 
	import NavBar from "components/Common/NavBar.vue";
	// js
	import Common from 'common/common.js';
	// Api
	import Api from "api/user_center.js";

	export default {
		name: "about",
		components: { NavBar },
		data(){
			return{
				logo: require('@/assets/images/icon_256x256.png'),
			}
		},
		methods:{
			// 
			gotoCompanyInfo(){
				Common.GotoPage('CompanyIntro', {}, this);
			}
		}
	};
</script>

<style lang="less" scoped>
	// 
	@import "../../assets/less/setting";

	.info{
		text-align: center;
		padding: 30*@rem 0;

		.icon{
			.wd(80);
			.mb(15);
		}
	}

	.info_list_item{
		.tips{
			background: @warn_color;
			color: #fff;
			font-style: normal;
			padding: 2*@rem 5*@rem;

			.ft(12);
			.border_radius(4);
			.mr(10);
		}

		.icon_next{
			color: #ddd;
			float: right;

			.mt(5);
			.ft(12);
		}
	}
</style>



