<template>
	<div id="userCenter">
		<!--  -->
		<NavBar title="" :show-msg="true"></NavBar>
		<!--  -->
		<div class="content">
			<!--  -->
			<Loading v-if="pageLoading"></Loading>
			<!--  -->
			<div v-else>
				<!--  -->
				<div class="cont_frame">
					{{ userAccount.userName }}
				</div>
				<ul class="user_info_list">
					<li class="cont_frame user_info_list_item" @click="gotoChangePwd">
						<i class="list_icon icon_password"></i><i class="icon_next"></i>
					</li>
					<li class="cont_frame user_info_list_item" @click="logOut">
						<i class="list_icon icon_exit"></i><i class="icon_next"></i>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	// 
	import NavBar from "components/Common/NavBar.vue";
	import Loading from "components/Common/Loading.vue";
	// js
	import Common from 'common/common.js';
	import { ClearAccount } from 'common/account.js';
	// Api
	import Api from "api/user_center.js";
	//Vuex
	import { mapGetters } from 'vuex';

	export default {
		name: "userCenter",
		components: { NavBar, Loading },
		computed: {
            ...mapGetters([ 'userAccount' ]),
        },
		data(){
			return{
				// 
				pageLoading: false
			}
		},
		methods:{
			// 
			gotoChangePwd(){
				Common.GotoPage('ChangePassword', {}, this);
			},
			// 
			logOut(){
				ClearAccount(this.$store.commit);
				Common.GotoPage('UnLogined', {}, this);
			}
		}
	};
</script>

<style lang="less" scoped>
	// 
	@import "../../assets/less/setting";

	/* user_info_list */

	.user_info_list_item{
		cursor: pointer;

		.list_icon{			
			color: @ic_gray_color;

			.mr(5);
		}

		.icon_next{
			color: #ddd;
			float: right;

			.mt(5);
			.ft(12);
		}
	}
</style>



