<template>
	<div id="msgCenter">
		<!--  -->
		<NavBar title="" :show-search="true"></NavBar>
		<!--  -->
		<div class="content">
			<div :class="['hint', showHint ? 'show_hint': '']" class="hint"></div>
			<!--  -->
			<Loading v-if="pageLoading"></Loading>
			<!--  -->
			<div v-else>
				<div v-if="!noList">
					<ul class="msg_list">
						<li v-for="(item, index) in declareList" :key="index" class="msg_ist_item cont_frame">
							<div class="title">{{ item.webSite }}</div>
							<NewsList :data="item.data" :num="item.data.length" :page-type="2"></NewsList>
						</li>
					</ul>
				</div>
				<div v-else class="no_msg_list">
					<p></p>
				</div>
				<!--  -->
				<div id="operateBar">
					<ul class="operate_bar">
						<li class="operate_item">
							<i class="operate_icon icon_readed" @click="showModel"></i>
							<span class="operate_title"></span>
						</li>
					</ul>
				</div>
				<!--  -->
				<BackTop v-show="showTopBtn"></BackTop>
			</div>
			<!--  -->
            <PopModel :show="showComfir" @close="hideModel">
                <div slot="content"></div>
                <div slot="footer">				
                    <button class="button fr" style="width: 48%;" @click="readAll"></button>
                    <button class="button cancel_btn fl" style="width: 48%;" @click="hideModel(false)"></button>
                </div>
            </PopModel>
		</div>
	</div>
</template>

<script>
	// 
	import NavBar from "components/Common/NavBar.vue";
	import Loading from "components/Common/Loading.vue";
	import NewsList from "components/Message/NewsList.vue";
	import BackTop from "components/Common/BackTop.vue";
	import PopModel from "components/Modal/PopModal.vue";
	// js
	import Common from 'common/common.js';
	// 
	import Modal from "mixins/modal.js";
	import ScrollPage from 'mixins/scrollPage.js';
	// Api
	import Api from "api/message.js";

	export default {
		name: "msgCenter",
		components: { NavBar, Loading, NewsList, BackTop, PopModel },
		mixins: [ ScrollPage, Modal ],
		data(){
			return{
				// 
				pageLoading: false,
				// 
				showHint: true,
				// 
				noList: true,
				// 
                showComfir: false,
				// 
				declareList: [],
				// ID
				idsList:[]
			}
		},
		created(){
			this.init();
		},
		mounted(){
			// 
			window.addEventListener('scroll', this.scrollPage);
		},
		methods:{
			init(){
				setTimeout(() => { 
					this.showHint = false;
				}, 1000);
				this.getMsg();
			},
			// 
			getMsg(){
				this.pageLoading = true;
				Api.GetMessage()
				.then(res => {
					this.pageLoading = false;
					if(res.code == 200){
						this.declareList = res.data.declareList;

						if(this.declareList.length > 0) this.noList = false;

						for(let i = 0; i < this.declareList.length; i++){
							let data = this.declareList[i].data;
							for(let j = 0; j < data.length; j++){
								this.idsList.push(data[j].id);
							}
						}
					}
					else this.showWarnModel(res.msg, 'warning');
				})
				.catch(err => console.log(err))
			},
			// 
			showModel(){
				this.showComfir = true;
			},
			// 
			hideModel(value){
				this.showComfir = value;
            },
			// 
			readAll(){
				this.hideModel(false);

				Api.Readed(this.idsList)
				.then(res => {
					this.pageLoading = false;
					if(res.code == 200){
						this.declareList = [];
						// 
						this.$store.commit('SET_UN_READ_COUNT', 0);
						this.showWarnModel('', 'success');
						this.noList = true;
					}
					else this.showWarnModel(res.msg, 'warning');
				})
				.catch(err => console.log(err))
			},
			// 
			scrollPage(){				
				let	scrollTop = $(window).scrollTop(),
					windowH = $(window).height(),
					documentH = $(document).height();

				if(scrollTop > windowH/2){
					this.showTopBtn = true;
				}
				else{
					this.showTopBtn = false;
				}
			}
		},
		destroyed(){
			// 
			window.removeEventListener("scroll",this.scrollPage);
		}
	};
</script>

<style lang="less" scoped>
	// 
	@import "../../assets/less/setting";
	@import "../../assets/less/operate_bar";

	/* hint */

	.hint{
		position:absolute;
		top: -30*@rem;
		background: rgba(0, 0, 0, 0.65);
		color: #fff;
		line-height: 30*@rem;
		text-align: center;
		-webkit-transition: all 0.3s ease;
		-moz-transition: all 0.3s ease;
		transition: all 0.3s ease;
		width: 100%;
		
		.ht(30);
		.ft(12);
	}

	.show_hint{
		display: block;
		top: 0;
	}

	/* msg_list */

	.msg_ist_item{
		.title{
			color: @base_color;
			font-weight: bold;

			.ft(16);
		}
	}

	/* no_msg_list */

	.no_msg_list{
		background: #fff;
		padding: 100*@rem 0;

		p{
			text-align: center;
		}
	}

	.operate_item{
		width: 100%;
	}
</style>



